'use strict';

// MODULES //

import copy from '@stdlib/utils-copy';
import CODES from './../data/data.json';


// MAIN //

/**
* Returns a mapping of standard library error codes to their corresponding error messages.
*
* @returns {Object} mapping of error codes to corresponding error messages
*
* @example
* var db = database();
* // returns {...}
*/
function database() {
	return copy( CODES );
}


// EXPORTS //

export default database;
